@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: rgb(18, 18, 18);
}

.moduleContainer {
  max-width: 1440px;
  margin: auto;
  padding: 20px;
}
.moduleContainer img {
  width: 100%;
  height: auto;
}
.moduleContainer .moduleContent h2 {
  font-size: 42px;
  font-weight: 700;
}
.moduleContainer .moduleContent p {
  margin-top: 12px;
  margin-bottom: 16px;
}
.moduleContainer .moduleContent button {
  width: 128px;
  height: 48px;
  padding: 16px 40px;
  background-color: rgb(228, 193, 174);
  border: none;
  border-radius: 32px;
  color: white;
  font-size: 16px;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  line-height: 100%;
}
.moduleContainer .moduleContent button:hover {
  cursor: pointer;
  background-color: rgba(228, 193, 174, 0.9);
  box-shadow: 0px 0px 10px -1px rgb(228, 193, 174);
}

.popupContainer {
  position: fixed;
  display: flex;
  width: 100%;
  height: 100%;
  background: rgba(18, 18, 18, 0.5);
  top: 0;
  left: 0;
}
.popupContainer .popup {
  width: 90%;
  max-width: 920px;
  height: 210px;
  background-color: white;
  margin: auto;
  position: relative;
  padding: 56px 0px 56px 25px;
}
.popupContainer .popup .xIcon {
  width: 24px;
  height: 24px;
  position: absolute;
  right: 2%;
  top: 0;
  font-size: 40px;
}
.popupContainer .popup .xIcon::after {
  content: "Ã—";
}
.popupContainer .popup .xIcon:hover {
  cursor: pointer;
}
.popupContainer .popup h2 {
  font-size: 42px;
  font-weight: 700;
  margin-bottom: 24px !important;
}

@media screen and (min-width: 768px) {
  .moduleContainer {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .moduleContainer .moduleContent {
    width: 380px;
  }
  .moduleContainer .moduleContent .desc {
    margin-top: 24px;
    margin-bottom: 32px;
  }
  .moduleContainer img {
    width: 42%;
    max-width: 505px;
    margin-right: 7%;
  }
  .popup {
    padding-left: 95px !important;
  }
}